@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&family=Roboto:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;

  font-family: 'Roboto', sans-serif;
}

.logo_decath {
  width: 200px;
  transform: rotate(-5deg);
}
.app {
  display: flex;
  justify-content: space-around;
}
header {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  transition-duration: 0.3s;

  min-height: 100vh;
}
.header-title {
  text-transform: uppercase;
  font-style: italic;

  font-size: 70px;

  margin: 40px 40px 0 40px;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
  text-align: right;
  z-index: 1;
}
.header-title:hover {
  cursor: pointer;
}
header:hover .underline {
  height: 30px;
  transition-duration: 0.3s;
}
.underline {
  transition-duration: 0.3s;
  height: 20px;

  position: absolute;
  z-index: 0;
}
.buline-1 {
  width: 160px;
  top: 70px;
  left: 40px;
}
.buline-2 {
  width: 100px;
  top: 150px;
  left: 40px;
}
.buline-3 {
  width: 120px;
  top: 230px;
  left: 40px;
}
.signature {
  position: fixed;
  bottom: 15px;
  left: 5%;
  margin: auto;
}
.signature img {
  margin-bottom: 20px;
}
.signature span {
  color: lightgray;
  font-style: italic;
}


.search-box-results {
  width: 100%;
  background-color: #eeecf3;
  padding-bottom: 100px;

  position: relative;
}


.intro-div {
  font-size: 20px;

  margin: 20px 0 0 30px;
}
.discover-new-sports {
  font-size: 30px;
  margin-top: 50px !important;
  margin-bottom: 40px !important;
}
.discover-new-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background-color: #eae8ed;

  width: 250px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;

  border: 1px solid lightgray;
  border-radius: 5px;

  padding: 15px;
  margin-bottom: 5px; 
  box-shadow: 2px 2px 10px -3px #000000;
}
.discover-title {
  text-transform: uppercase;
  font-style: italic;
  text-align: center;
  margin: 12px 0;
}
.discover-new-box img {
  width: 200px;
}
.discover-desc {
  word-wrap: break-word;
  width: 100%;
  text-align: justify;
}
.discover-list {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  width: 90%;
}
.learn-more-btn-discover {
  margin-top: 20px;
}
.search-box {
  background-color: #0082C3;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.allSearchBox ul {
  z-index: 1;
  position: absolute;
  top: 110px;
  left: 15px;

  width: 400px;
  max-height: 500px;
  overflow-y: scroll;

  list-style-type: none;

  border: 1px solid lightgray;
  border-radius: 10px;
  background-color: white;
}
.allSearchBox ul li {
  font-size: 20px;
  padding: 10px 0 10px 30px;
  border-radius: 10px;

  transition-duration: 0.2s;
}
.allSearchBox ul li:hover {
  transition-duration: 0.2s;
  transform: scale(1.1);

  background-color: lightgray;
  cursor: pointer;
}
.search-icon {
  width: 30px;
  height: 30px;
  margin-right: 15px;
}
.textInput {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.vtmn-text-input_container, .vtmn-text-input {
  width: 100%;
}
.container {
	width: 100%;
	position: relative;
}
.sport-all {
	margin: 30px 8% 50px 30px;
}
.sport-title {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}
.sport-title:hover .blackUnderlineTop {
	transform: translateX(60px) skew(-30deg);
	transition-duration: 0.2s;
}
.sport-title:hover .blackUnderline {
	transform: translateX(-60px) skew(-30deg);
	transition-duration: 0.2s;
}
.sport-title h1 {
	text-transform: uppercase;
	font-style: italic;
	font-size: 64px;

	position: relative;
}
.blackUnderlineTop {
	margin: 50px 220px 10px 0;
}
.blackUnderline, .blackUnderlineTop {
	width: 70px;
	height: 15px;
	background-color: #000;
	transform: skew(-30deg);
	transition-duration: 0.2s;
}
.sport-content {
	margin-top: 30px;
}
.sport-content-title {
	margin: 30px 0 10px 0;
	text-transform: uppercase;
	font-style: italic;
	font-weight: bold;
	font-size: 20px;
}
.sport-imageList {
	display: flex;
	flex-wrap: wrap;
}
.related-sports {
	margin-top: 40px;
}
.related-sports ul {
	list-style-type: circle;
	margin-left: 30px;
}
.related-sports ul li {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}
.related-sports ul li:hover {
	text-decoration: underline;
	cursor: pointer;
}
.sport-image {
	width: 500px;
}
.loadingImage {
	width: 500px;
	background-color: lightgray;
	height: 300px;
}
